import React, { useEffect, useState } from "react";
import { parse } from "qs";
import { HeaderSeparator, Button } from "../../components";
import styles from "./styles.module.css";

const FrontPageInvitation = ({ setInvitationOpen }) => {
  const [invTo, setInvTo] = useState("");

  useEffect(() => {
    const invTo = parse(window.location.search.substring(1));
    setInvTo(invTo.to);
  }, []);

  return (
    <div className={styles.pageContainer}>
      {/* Floating Hearts Animation */}
      <div className={styles.heartsContainer}>
        {[...Array(20)].map((_, i) => (
          <div key={i} className={`${styles.heart} ${styles[`heart${i % 5}`]}`} />
        ))}
      </div>

      {/* Invitation Card with Flip Animation */}
      <div className={styles.cardContainer}>
        <div className={styles.card}>
          <div className={styles.cardFront}>
            <p className={styles.welcomeText}>You're Invited!</p>
          </div>
          <div className={styles.cardBack}>
            <div className={styles.separatorContainer}>
              <HeaderSeparator />
              <p className={styles.textName}>Elangovan &amp; Indhumathi</p>
              <HeaderSeparator />
            </div>

            <p className={styles.invitationText}>
              {invTo && (
                <span>
                  Dear <br />
                  <span className={styles.invitationTo}>{invTo}</span>
                  <br />
                </span>
              )}
              We are getting married, and we want you <br /> to be part of our
              special day!
            </p>

            <div className={styles.buttonContainer}>
              <Button
                text="Open Invitation"
                onClick={() => setInvitationOpen(true)}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FrontPageInvitation;
